'use client';
import { useState, useEffect } from 'react';
import { useDynamicContext, useIsLoggedIn } from "@dynamic-labs/sdk-react-core";
import { userClient, smartAccountClient, PAYMASTER_ADDRESS } from '@/lib/fastlane/user';
import { shBundler } from '@/lib/fastlane/user';
import './Methods.css';

export default function DynamicMethods({ isDarkMode }) {
  const isLoggedIn = useIsLoggedIn();
  const { sdkHasLoaded, primaryWallet } = useDynamicContext();
  const [isLoading, setIsLoading] = useState(true);
  const [result, setResult] = useState('');

  
  const safeStringify = (obj) => {
    const seen = new WeakSet();
    return JSON.stringify(obj, (key, value) => {
      if (typeof value === 'object' && value !== null) {
        if (seen.has(value)) {
          return '[Circular]';
        }
        seen.add(value);
      }
      return value;
    }, 2);
  };

  useEffect(() => {
    if (sdkHasLoaded && isLoggedIn && primaryWallet) {
      setIsLoading(false);
    } else {
      setIsLoading(true);
    }
  }, [sdkHasLoaded, isLoggedIn, primaryWallet]);

  function clearResult() {
    setResult('');
  }

  async function fetchWalletClient() {
      if(!smartAccountClient) return;
      setResult(safeStringify(smartAccountClient));
  }

  async function callGetSignature(userOp, validUntil, validAfter, paymasterAddress, chainId) {
      // Build the JSON-RPC payload
      const payload = {
        jsonrpc: "2.0",
        id: 1,
        method: "backend_getSignature",
        params: { userOp, validUntil, validAfter, paymasterAddress, chainId },
      };
  
      try {
        const response = await fetch('/api/paymaster', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(payload)
        });
  
        const data = await response.json();
  
        if (data.error) {
          console.error("Error from backend:", data.error);
          return;
        }
  
        console.log("Sponsor Signature received:", data.result);
      } catch (error) {
        console.error("Fetch error:", error);
      }
  }

  async function sendSponsoredTransaction() {
    if(!smartAccountClient) return;
    const calls = [
      {
        to: userClient.account.address,
        value: 1000000000000000n,
      },
    ];
    
    const userOp = await smartAccountClient.prepareUserOperation({
      account: smartAccount,
      calls,
    });
    
    // BACKEND SERVICE: START
    const currentTime = BigInt(Math.floor(Date.now() / 1000));
    const validUntil = currentTime + BigInt(3600);
    const validAfter = BigInt(0);
    
    const sponsorSignature = await callGetSignature(userOp, validUntil, validAfter, PAYMASTER_ADDRESS, 10143);
    console.log("Sponsor Signature:", sponsorSignature);

    const userOpHash = await smartAccountClient.sendUserOperation({
      account: smartAccountClient.account,
      calls: [
        {
          to: userClient.account.address,
          value: 1000000000000000n,
        },
      ],
      paymasterContext: {
        mode: "sponsor",
        sponsor: sponsorEOA.address,
        sponsorSignature: sponsorSignature,
        validUntil,
        validAfter
      }
    });
    
    console.log("User Operation Hash:", userOpHash);
    
    const userOpReceipt = await shBundler.waitForUserOperationReceipt({
      hash: userOpHash,
    });
    console.log("User Operation Receipt:", userOpReceipt);
  }



   return (
    <>
      {!isLoading && (
        <div className="dynamic-methods" data-theme={isDarkMode ? 'dark' : 'light'}>
          <div className="methods-container">
            
            
    {primaryWallet && isEthereumWallet(primaryWallet) &&
      <>
        <button className="btn btn-primary" onClick={fetchWalletClient}>Fetch Wallet Client</button>
        <button className="btn btn-primary" onClick={sendSponsoredTransaction}>Send Sponsored Transaction</button>    
      </>
    }

        </div>
          {result && (
            <div className="results-container">
              <pre className="results-text">{result}</pre>
            </div>
          )}
          {result && (
            <div className="clear-container">
              <button className="btn btn-primary" onClick={clearResult}>Clear</button>
            </div>
          )}
        </div>
      )}
    </>
  );
}